import React from "react";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import EnquiriesForm from "./EnquiriesForm";
import { Button } from "./ui/button";

const FormDailog = () => {
  return (
    <Dialog>
      <DialogTrigger className="w-full">
        <p className="bg-primary text-primary-foreground px-3 py-2 rounded-md w-full text-sm font-semibold">Enquiries Now </p>
      </DialogTrigger>
      <DialogContent>
        <DialogTitle></DialogTitle>
        <EnquiriesForm />
      </DialogContent>
    </Dialog>
  );
};

export default FormDailog;
